import { h } from "preact"
import styled from "styled-components"
import Icon, { WithTooltip } from "../Utils/Icon"
import Play from "./play.svg"
import Add from "./add.svg"
import Remove from "./remove.svg"

const Card = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  position: relative;
  border-radius: 4px;
  padding: 5px;

  background-color: white;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.08);
  cursor: pointer;
`

const Artwork = styled.div`
  background-size: cover;
  background-position: center;
  display: flex;
  min-width: 55px;
  min-height: 55px;
  margin-right: 5px;

  > img {
    display: none;
  }

  @media screen and (min-width: 500px) {
    > img {
      opacity: 0;
      transform: scale(0);
      transition: all 200ms ease;
      margin: auto;
      display: block;
    }

    &:hover {
      > img {
        opacity: 1;
        transform: scale(1);
      }
    }
  }
`

const Container = styled.div`
  position: relative;
  flex: 1;
`

const Artist = styled.p`
  color: #999;
  font-size: 0.75rem;
`

const Music = styled.p`
  color: #444;
  font-size: 0.9rem;
`

const Wrapper = styled.div`
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
`

const Info = styled.span`
  margin-right: 15px;
  font-size: 0.8rem;
  color: #444;
`

const Duration = Info.extend`
  flex: 1;
`

export const CardContainer = styled.div`
  position: relative;
  padding-bottom: 60px;
  margin-bottom: 40px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));

  @media screen and (min-width: 500px) {
    grid-template-columns: repeat(auto-fit, 270px);
    grid-gap: 15px;
  }
`

export const SongCard = ({ song, from, index, play, playlistAction }) => {
  const { title, user, artwork, duration, likes, likesMin } = song
  const fromPlaylist = from === "/playlist"
  return (
    <Card onClick={() => play(index)}>
      <Artwork style={{ backgroundImage: `url(${artwork})` }}>
        <Icon src={Play} size={30} />
      </Artwork>

      <Container>
        <Artist>{user}</Artist>
        <Music>{title}</Music>

        <Wrapper>
          <Duration>{duration}</Duration>

          <WithTooltip tooltip={`${likes} likes`}>
            <Info>{likesMin}</Info>
          </WithTooltip>

          <WithTooltip
            tooltip={fromPlaylist ? "Remove from Playlist" : "Add to Playlist"}
          >
            <Icon
              onClick={e => playlistAction(e)(song)}
              src={fromPlaylist ? Remove : Add}
              size={16}
              alt=""
            />
          </WithTooltip>
        </Wrapper>
      </Container>
    </Card>
  )
}



